<template>
    <div>
        <div class="kd-page-title">{{title}}</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <text-table :data="getDataArr()"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button v-if="!isComplete" icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button v-if="!isComplete" icon="kedaIcon-duihao2" text="确认" :button-handle="nextStep"/>
            <kd-button v-if="isComplete" icon="kedaIcon-policework" text="业务完成" typeClass="complete"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import { playAudio } from '@/utils/media';
import { translateLicense } from '@/utils/tools';

export default {
    name: 'text-appear',
    components: { textTable },
    props: {
        isComplete: { type: Boolean, default: false },
        title: { type: String, default: '信息确认' },
        media: { default: null },
        formListData: { type: Object, default: () => {} },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        async nextStep () {
            await this.nextFunction();
        },
        getDataArr () {
            return translateLicense(this.formListData);
        }
    },
    mounted () {
        if (this.media) {
            this.mediaObject = playAudio(this.media);
            this.mediaObject.play();
        }
    },
    beforeDestroy () {
        if (this.mediaObject) {
            this.mediaObject.destroy();
        }
    }
};
</script>

