<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <text-appear v-if="currentStep === steps.findIndex(step=>step.key === 'driverInfo')" :formListData="driverLicenseInfo"></text-appear>
        <photo-tip-second
            v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')"
            tipText="是否确认提交驾驶证照片更新业务？"
            :openAlert="true"
            :nextFunction="photoNext"
            :photoNumber="3"
        />
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'processComplete')"
            :handle-business="completeBusiness"
        />
        <complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="result"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import complete from '@/components/complete';
import process from '@/components/process';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import textAppear from '@/view/pageComponents/text-appear';
import { DRIVER_PHOTO_RENEWAL } from '@/constant/stepsOption';
import { getDriverPhotoLicenseInfo, getDriverPhotoLicenseComplete, driverLicensePhotoUpload } from '@/service/traffic/traffic';
import { handleDriverPhotoRequest } from '@/utils/axiosHandle';
import { cancelPrefix } from '@/utils/tools';

export default {
    name: 'DriverExchangeEvidenceIndex',
    components: { identifyCardPage, faceYitu, contrast, complete, process, textAppear, photoTipSecond },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        })
    },
    data () {
        return {
            result: {},
            driverLicenseInfo: {}
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async handleBusiness () {
            const res = await getDriverPhotoLicenseInfo({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                this.driverLicenseInfo = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, imgName: 'imgDriverPerson' });
            }
        },
        async completeBusiness () {
            const res = await getDriverPhotoLicenseComplete({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                this.result = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    imgName: 'imgDriverPerson'
                });
            }
        },
        async photoNext (photoBase) {
            let res = await handleDriverPhotoRequest(photoBase, this.CONSTANT);
            if (res && res.code === 1) {
                res = await driverLicensePhotoUpload({
                    identifiedPhoto: cancelPrefix(res.obj),
                    idNum: this.identifyCard.certNumber
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({ message: res.message, imgName: 'imgDriverPerson' });
                }
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        }
    },
    mounted () {
        this.setSteps(DRIVER_PHOTO_RENEWAL);
    }
};
</script>
